.buttons {
  display: flex;
  flex-direction: column;
}
a {
  text-decoration: none;
}
ion-text {
  padding-left: 5px;
}
#faq {
  margin-top: 20px;
  border-top: 1px solid var(--ion-color-grey-shade);
}
ion-modal#example-modal {
  --width: fit-content;
  --min-width: 250px;
  --max-width: 80vw;
  --height: fit-content;
  --border-radius: 6px;
  --box-shadow: 0 28px 48px rgba(0, 0, 0, 0.4);
}

ion-modal#example-modal .wrapper {
  padding: 20px;
}

.compact-list {
  --ion-item-padding-start: 0;
  --ion-item-padding-end: 0;
  --ion-item-padding-top: 2px;
  --ion-item-padding-bottom: 2px;
}

.compact-item {
  --min-height: 24px;
}
